.feedback-form {
  max-width: 480px;
  background-color: rgba(214, 213, 213, .88);
  border: 1px solid rgba(0, 0, 0, .992);
  border-radius: 4px;
  flex-direction: column;
  margin: 0 auto;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

